body {
  margin: 0;
  font-family: 'Angsana New';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.card {
  display: flex;
  flex-direction: column;
  background: #fff;
  color: #3f4448;
  border-radius: 2px;
  min-width: 575px;
  min-height: 212px;
  padding: 4px;

  font-size: medium;
  box-shadow: 0 0 10px 1px #00000064;
}

.card > .card-header {
  position: relative;
  color: #4800ff;
  font-size: 52px;
  font-weight: bold;
  margin: 24px;
}
.card > .card-body {
  position: relative;
  width: 100%;
}
.card > .card-footer {
  position: relative;
  width: 100%;
}

.card > .card-body input {
  display: inline-flex;
  justify-content: center;
  width: 92%;
  padding: 12px;
  border: 1px solid #e4e4e4;
  border-radius: 2px;
  outline: none;
}

.card > .card-footer .list {
  margin: 0;
  padding: 0;
  padding-top: 18px;
}

.card > .card-footer .list .list-item {
  position: relative;
  display: flex;
  flex-direction: row;

  border-top: 1px solid #e4e4e4;

  margin-left: 12px;
  margin-right: 12px;
  padding: 6px;
  padding-left: 4px;
  padding-right: 4px;

  text-align: left;
  align-self: center;

  font-size: x-large;
}

.card > .card-footer .list .list-item:first-child {
  margin-bottom: 0;
}
.card > .card-footer .list .list-item:last-child {
  margin-top: 0;
  border-bottom: 1px solid #e4e4e4;
}

.card > .card-footer .list .list-item div:not(:first-child) {
  padding: 2px;
}

.card > .card-footer .list .list-item div,
.card > .card-footer .list .list-item input {
  align-self: center;
}

.btn {
  cursor: pointer;
  display: inline-flex;
  position: relative;
  align-items: center;
  justify-content: center;

  background: none;
  color: #3f4448;

  margin: 2px;
  border: none;
  outline: none;

  transition: background 500ms;
}

.btn svg {
  pointer-events: none;
}

.btn:hover {
  background: #ecf0f1;
}

.btn.fab {
  height: 30px;
  width: 30px;

  border-radius: 50%;
}

input[type='checkbox'] {
  border-radius: 0px !important;
  border: 1px solid;
}

input[type='checkbox']:checked + div {
  text-decoration: line-through;
  color: #7d7d7d !important;
}

div[contenteditable='true'] {
  box-shadow: inset 0 0 0 0.8px #e4e4e4;
  outline: none;
}
